body {
  font-family: 'Pacifico', cursive;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
  font-family: 'Pacifico', cursive;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center
}

.game__title{
  text-align: center;
  margin-bottom:60px;
}

.game-info {
  margin-left: 20px;
}

.board-row:nth-child(2) button:nth-child(1){
  border-top:none;
  border-left:none;
}

.board-row:nth-child(2) button:nth-child(2){
  border-top:none;
}

.board-row:nth-child(2) button:nth-child(3){
  border-top:none;
  border-right:none;
}

.board-row:nth-child(3) button:nth-child(1){
  border-left:none;
}

.board-row:nth-child(3) button:nth-child(3){
  border-right:none;
}

.board-row:nth-child(4) button:nth-child(1){
  border-bottom:none;
  border-left:none;
}

.board-row:nth-child(4) button:nth-child(2){
  border-bottom:none;
}

.board-row:nth-child(4) button:nth-child(3){
  border-bottom:none;
  border-right:none;
}


@media (min-width:576px){
  .square{
    width:100px;
    height:100px;
    font-size:50px;
  }
}

@media (min-width:768px){
  .square{
    width:100px;
    height:100px;
    font-size:50px;
  }

  .game__title{
    font-size:50px;
  }
}



